import React from "react"

import { programConfig } from "../Config"
import { authorLink } from "../Helpers"

const About = () => {
  return (
    <div className="container mt-5">
      <h1>About</h1>
      <p>
        <strong>{programConfig.name}</strong> is a simple, easy to use alternative
        to most image gallery's UI. Made using React by {authorLink("github", programConfig.author.github)}.
      </p>
      <h3>To-do</h3>
      <ul>
        <li>Implement blacklist functionality.</li>
        <li>Add <code>order:</code>, <code>date:</code>, etc sliders to search box.</li>
        <li>Create a logo (and add <code>favicon.ico</code> / <code>logo192.png</code>).</li>
      </ul>
      <h3>v0.1.1</h3>
      <ul>
        <li>Renamed <code>Sites</code> page to <code>Source</code>.</li>
        <li>Updated search box on the front page to be more centered.</li>
      </ul>
      <h3>v0.1.0</h3>
      <ul>
        <li>Initial release.</li>
      </ul>
    </div>
  )
}

export default About