import React from "react"

const getFromPath = (path, resp) => {
  let data = resp

  for (let i = 0; i < path.length; i++) {
    data = data[path[i]]
  }

  return data
}

const determineFileType = (file) => {
  let path = file.split(".")
  let ext = path[path.length - 1]

  let post_type = "none"

  if (["mp4", "webm"].includes(ext)) {
    post_type = "video"
  } else if (ext === "gif") {
    post_type = "gif"
  } else if (ext === "swf") {
    post_type = "flash"
  } else {
    post_type = "image"
  }

  return post_type
}

const capitalizeString = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

const authorLink = (service, user) => {
  let url = null

  switch (service) {
    case "github":
      url = "https://github.com/" + user
      break
    default:
      break
  }

  return (
    <span>
      <strong>
        <a href={url}>@{user}</a>
      </strong> on <strong>
        {capitalizeString(service)}
      </strong>
    </span>
  )
}

export { getFromPath, determineFileType, capitalizeString, authorLink }